.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  display: flex;
  background-color: rgb(255, 231, 200);
  flex-direction:row;
  gap: 10px;
}

.frame {
  flex: 1 1 300px;
  border: 1px solid black;
  height: 700px;
  padding: 10px;
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.category-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4a90e2;
  color: white;
  border: none;
  cursor: pointer;
}

.action-list {
  margin-top: 10px;
}

.action-block {
  margin-bottom: 5px;
  background-color: #ef9439;
  padding: 5px;
  cursor: pointer;
}

.action-block:hover {
  background-color: #d1d1d1;
}

.image-container {
  margin-top: 20px;
  text-align: center;
  height:5px;
  width:5px;
}

.uploaded-image {
  max-width: 100%;
  height: auto;
}

.header {
  background-color: purple;
  padding: 40px;
  width: 450px;
}

.header-text {
  justify-content: center;
  color:rgb(255, 119, 0);
}
.list-text{
  color:yellow;

}
.button-container{
  display: flex;
  background-color: rgb(175, 224, 96);
  flex-direction:column;
  height:700px;
  padding-right:10px;
}


.help-box {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #4cd2ed;
  padding: 10px;
  border: 1px solid #4cd2ed;
}
.category-button{
  background-color: red;
}

.hello-box{
  width: 6em;
  border: 2px solid #99f535;
  box-shadow: 9px 6px 4px #5B2C6F;
  padding: 7px 11px;
  background-image: linear-gradient(150deg, #dada47 , #ffef41 30%,#fdee4b );
}

